import React, { useState } from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption
} from 'reactstrap';
//import tes from '../image/tes.jpg'



const items = [
  {
    id: 1,
    altText: 'Slide 1',
    caption: <b>WELCOME TO CONTACT MANAGER</b>,
    small:<b>Easy way to maintain your contacts</b>
  },
  {
    id: 2,
    altText: 'Slide 2',
    caption: 'REGISTER',
    small:""
  },
  {
    id: 3,
    altText: 'Slide 3',
    caption: 'LOGIN',
    small:""
  },
  {
    id: 4,
    altText: 'Slide 4',
    caption: 'MAINTAIN',
    small:""
  }
];

const Slider = (props) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  }

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  }

  const goToIndex = (newIndex) => {
    if (animating) return;
    setActiveIndex(newIndex);
  }

  const slides = items.map((item) => {
    return (
      <CarouselItem
        className="custom-tag"
        tag="div"
        key={item.id}
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
      >
        <CarouselCaption className="text-danger" captionText={item.small} captionHeader={item.caption} />
      </CarouselItem>
    );
  });

  return (
    <div className="collapse navbar-collapse" className="container">
      <style>
        {
          `.custom-tag {
              max-width: 100%;
              height: 150px;
              background:  ;
            }`
        }
      </style>
      <Carousel
        activeIndex={activeIndex}
        next={next}
        previous={previous}
      >
        <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={goToIndex} />
        {slides}
        <CarouselControl direction="prev" directionText="Previous" onClickHandler={previous} />
        <CarouselControl direction="next" directionText="Next" onClickHandler={next} />
      </Carousel>
    </div>
  );
}

export default Slider;